@font-face {
  font-family: 'orbitron';
  src: url('./font/orbitron/Orbitron-VariableFont_wght.woff2') format('woff2'),
      url('./font/orbitron/Orbitron-VariableFont_wght.woff') format('woff'),
      url('./font/orbitron/Orbitron-VariableFont_wght.ttf')  format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
    background-color: #020f1b;
    font-family: 'orbitron';
}

   /* Video */
.Video {

  z-index: -10;
  min-height: 100vh;
  
}

.main {
  margin-bottom: 20vh;
  
}


.transparent, .swal2-container {
  background-color: rgba(255, 0, 0, 0.123);
}

/* Buttons */
.glow-on-hover {
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.btn-desert {

  color: #48ff00 !important;
  background: #111;
}

.btn-fire {

  color: #ff7300 !important;
  background: #111;
}

.btn-storms {

  color: #002bff !important;
  background: #111;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:active {
  color: #000
}

.glow-on-hover:active:after {
  background: transparent;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}